@import url("https://fonts.googleapis.com/css?family=Inter:regular,700,900&display=swap$subset=cyrillic-ext");

::before,
::after {
  padding: 0px;
  margin: 0px;
  border: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol,
li {
  list-style: none;
  padding-inline-start: 0;
  margin-block-start: 0;
  margin-block-end: 0;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
  margin: 0;
}

html,
body {
  margin: 0;
  height: 100%;
  line-height: 1;
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
  font-family: "Inter", sans-serif;
  background-color: #2a2c2f;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}
